@import '../../../_sass/abstracts/variables';

.nav-burger-box {
    display: flex;
    flex-direction: column;
    height: auto;
    justify-content: space-around;
    align-items: center;

    &:hover {
        cursor: pointer;
    }
}

.fade-in-menutitle {
    opacity: 1;
    transition: 0.2s ease-in-out;
    color: $color-dark-grey;
}

.fade-out-menutitle {
    opacity: 0;
    transition: 0.2s ease-in-out;
}

.menu-action {
    transition: 0.2s ease-in-out;

    &:hover .nav-burger-top {
        cursor: pointer;
        transform: scale(1.2);
        transition: 0.2s ease-in-out;
        transition-timing-function: cubic-bezier(.17, .67, .83, .67);
    }

    &:hover .nav-burger-bottom {
        cursor: pointer;
        transform: scale(0.0);
        transition-timing-function: cubic-bezier(.17, .67, .83, .67);
    }
}

.fade-in-cross {
    height: auto;
}

.nav-burger-top {
    width: 45px;
    height: 1px;
    background: $color-dark-grey;
    transition: 0.2s ease-in-out;
}

.nav-burger-bottom {
    width: 45px;
    height: 1px;
    background: $color-dark-grey;
    transition: 0.2s ease-in-out;
    margin-top: 10px;
}

.nav-burger-top-wh {
    height: 1px;
    background: #fff;
    transform: rotate(45deg);
    width: 20px;
    margin-top: 19px;
    transition: 0.2s ease-in-out;
}

.nav-burger-bottom-wh {
    height: 1px;
    background: #fff;
    transform: rotate(-45deg);
    width: 20px;
    transition: 0.2s ease-in-out;
}

.nav-fixed {
    display: flex;
    align-items: flex-end;
    position: fixed;
    width: 100%;
    padding: 0px 10%;
    left: 0;
    right: 0;
    top: 0;
    z-index: 9994;
    height: 80px;
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
    box-sizing: border-box;
}